.postcards__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, 282px);
  list-style: none;
  gap: 20px 17px;
  justify-content: center;
  padding: 0;
  margin: 0;
}

@media screen and (min-width: 730px) and (max-width: 916px) {
  .postcards__list {
    grid-template-columns: repeat(auto-fit, 260px);
  }
}
